/* Header start */
main{
  width: 1300px;
}
main header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #009879;
  color: white;
  padding: 0 1rem;
  border-bottom: 1px solid #009879;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

main header h1{
  font-size: 2rem;
}

main header .header_left{
  display: flex;
  align-items: center;
}

main header .header_left .manual{
  margin: 0 1rem;
  background-color: #4472C4;
  padding: 4px 8px;
  border: 1.5px solid #283853;
  border-radius: 6px;
  transition: transform .2s linear;
}

main header .header_left .manual:hover{
  transform: scale(1.1);
}

main header .header_left .manual a{
  color: white;
}


main header .logo {
  padding: .21rem 0 0.1rem;
  text-align: center;
}

main header .img_div img {
  position: relative;
  width: 100%;
  max-width: 150px;
  background: white;
  padding: 5px;
  border-radius: 5px;
}
main header .logo p {
  /* padding-top: 0.5rem; */
}
main header #version{
  font-size: .7rem;
}

/* Header end */
