/* stoch_info section start*/
section#stock_info {
  margin: 0.31rem 0 0.2rem;
  display: block;
}
h3 {
  /* text-align: start; */
  margin-bottom: 0.1rem;
  letter-spacing: 6px;
  color: #2d2d2d;
}
.tb_container{
  /* display: block;
  height: 201px;
  overflow-y: scroll; */
}

.tb_container thead{
  text-align: left;
  position: sticky;
  top: 0;
  height: 32.8px;
  width: calc(100% - 1rem);
}

.tb_container thead::after{
  content: "";
  position: absolute;
  top: 0;
  right: -1rem;
  width: 1rem;
  background: var(--table-color);
  height: 32.8px;
}
#stock_table {
  border-collapse: collapse;
  font-size: 0.9rem;
  min-width: 400px;
  width: 100%;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  table-layout: fixed;
}

#stock_table thead tr {
  background-color: var(--table-color);
  color: var(--body-bg);
  text-align: center;
  font-weight: bold;  
  height: 32.8px;
}

#stock_table th,
#stock_table td {
  padding: 7px;
  border-right: 1px solid #dddddd;
  overflow-wrap: break-word;
}

#stock_table th:last-of-type,
#stock_table td:last-of-type{
  border-right: 0px;
}

#stock_table tbody {
  display: block;
  height: 168px;
  overflow-y: scroll;
}
thead,
tbody tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}


.out_loc_cd{
  width: 130px;
}
.out_loc_cd_name{
  max-width: 300px;
}

.lot_no, .sub_lot_no{
  width: 130px;
}

.out_line_cd{
  width: 100px;
}
thead .unit_qty{
  width: 75px;
}

tbody .unit_qty{
  width: 75px;
  text-align: right;
}

.radio_check{
  width: 80px;
  text-align: center;
}

#stock_table tbody tr {
  border-bottom: 1px solid #dddddd;
}
/* #stock_table tbody tr:hover{
      font-weight: bold;
      color: var(--table-color);
    } */

#stock_table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

#stock_table tbody tr:last-of-type {
  border-bottom: 2px solid var(--table-color);
}

#stock_table th,
#stock_table tbody tr {
  /* text-align: center; */
}

/* stoch_info section end*/
