/* color names */
:root {
  --body-bg: #ffffff;
  --table-color: #009879;
}
/* reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* link decoration reset */
a {
  text-decoration: none;
}
/* list style none */
li {
  list-style: none;
}
body {
  background: var(--body-bg);
  display: grid;
  place-content: center;
}
#root{
  margin: 0 2rem;
}
main{
  margin: 0 auto;
}

