/* details section start */
section#details {
  display: block;
}
section#details h3 {
  text-align: start;
  width: fit-content;
  margin-bottom: 0.1rem;
  color: #2d2d2d;
  transform: translate(17px, 14px);
  background: #fafafa;
  padding: 0 0.3rem;
}

section#details form {
  /* padding: 1rem 0; */
  outline: 2px solid rgba(0, 0, 0, 0.6);
}

section#details form section {
  display: flex;
  align-items: center;
}
section#details form section .sec_one,
section#details form section .sec_two {
  display: flex;
  align-items: baseline;
  /* justify-content: start; */
  position: relative;
  width: 50%;
  padding: 0 1rem;
}
section#details
  form
  section
  .sec_one
  .Mui-disabled.MuiInputBase-formControl.MuiInput-formControl,
section#details
  form
  section
  .sec_two
  .Mui-disabled.MuiInputBase-formControl.MuiInput-formControl {
  width: 100%;
}

#details > form > section:nth-child(4) {
  margin-top: 0.7rem;
}

#details form div.sec_one div,
#details form div.sec_two div {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
button {
  margin-right: 0.5rem;
}
.Mui-disabled {
  color: black !important;
}

.MuiInputBase-input {
  font-size: 14px !important;
}

.MuiFormLabel-root {
  font-size: 0.7rem !important;
}
.MuiInputLabel-outlined.MuiInputLabel-shrink {
  transform: translate(14px, -4px) scale(0.75) !important;
}

.PrivateNotchedOutline-legendLabelled-4 {
  font-size: 0.52em !important;
}

.MuiOutlinedInput-inputMarginDense {
  padding-top: 6px !important;
  padding-bottom: 6px !important;
}

.MuiButton-outlinedSizeSmall {
  font-size: 0.8125rem;
  padding: 2px !important;
}

.makeStyles-root-12 .MuiTextField-root {
  margin: 5px !important;
}

.fixed_width {
  width: 362px !important;
}

#details > form > section:nth-child(5) > div.sec_one > div:nth-child(2) {
  width: 287px !important;
}

.just-space-between {
  justify-content: space-between;
}

#details
  > form
  > section.customInput
  > div.sec_two.just-space-between
  > div
  > div {
  width: 100% !important;
}

#details > form > section.customInput > div.sec_one > div > div {
  width: 100% !important;
}

/* hides number input arrows */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
}

#unit_qty{
  text-align: right;
}

/* label font size change after build */
.jss4{
  font-size: 0.52em !important;
}