/* search_form section start */

section#search_form form {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
#search-contents,
#search {
  width: 50%;
  padding-bottom: 0.5rem;
}
#search-contents {
  padding-right: 2rem;
}
#search {
  display: flex;
  align-items: flex-end;
  justify-content: inherit;
}

#search .sakusei_info {
  margin: 0 8px;
}

#search .place_code {
  display: flex;
  align-items: baseline;
}
#search .place_code div:first-child {
  display: flex;
  align-items: center;
}

#search-contents section {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

#search-contents section .div {
  display: flex;
  justify-content: center;
  align-items: center;
}
#search-contents section .label_input {
  margin: 0 0.8rem;
}

section.prep_pattern_code {
  display: flex;
  align-items: baseline;
  overflow: hidden;
}

fieldset {
  border-color: "#ff0000" !important;
  border-width: 2px !important;
}

.MuiInputLabel-shrink {
  transform: translate(0, 7.5px) scale(0.75) !important;
}
.seach_btn_div {
  padding-right: 1rem;
}

#search > section > div.MuiFormControl-root.MuiTextField-root.saku_ptn > div {
  width: 287px;
}

#saku_kbn_search {
  text-align: center;
}

.sakusei_basho{
  width: calc(100% - 120px);
}
/* search_form section end */
